.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-button {
  transition: 0.2s ease-out;
  border: 3px solid;
  border-color: #738a59;
  background: none;
  color: white;
  padding: 10px;
  font-size: 16px;
  margin: 0;
}

.dropdown-content {
  display: none;
  position: absolute;
  min-width: 140px;
  width: 100%;
  z-index: 1;
}

.dropdown-content button {
  width: 100%;
  margin: 0;
  padding: 12px 16px;
  display: block;
  text-decoration: none;
  border-radius: 0;
}

.dropdown-content button:hover {
  background-color: #dddddd;
  cursor: pointer;
}

.dropdown-content button:active {
  background-color: #cecece;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown:hover .dropdown-button {
  background-color: #738a59;
  color: black;
}
/*# sourceMappingURL=Dropdown.css.map */
.help {
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2;
}

.help-hide {
  display: none;
}

.help-show {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.help-buttons {
  width: 50vw;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  z-index: 1;
}

.help-content {
  z-index: 2;
  display: flex;
  width: 50vw;
  height: 80vh;
  background-color: white;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  align-items: flex-start;
  justify-content: center;
}

.help-content h2 {
  color: black;
  -ms-grid-row-align: center;
  align-self: center;
}

.help-page-button {
  width: auto;
  height: 40px;
  border-radius: 10px;
  background-color: white;
  position: relative;
  top: 8px;
  transition: 0.2s;
  padding: 5px;
}

.x {
  height: 30px;
  position: relative;
  bottom: 3px;
}

.tabs {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.tabs div {
  margin-right: 1px;
  padding-right: 10px;
  padding-left: 10px;
}

.active {
  background-color: white;
}

.active:hover {
  cursor: default;
}

.inactive {
  background-color: #cecece;
}

.inactive:hover {
  background-color: #e7e7e7;
  cursor: pointer;
}

h4 {
  font-weight: normal;
}

.exit-button:hover {
  transform: translateX(-0.13em) scale(1.1);
  cursor: pointer;
}

@media screen and (max-width: 1350px) {
  .help-content,
  .help-buttons {
    width: 75vw;
  }
}

@media screen and (max-width: 1000px) {
  .help-content {
    width: 100vw;
    height: auto;
    position: absolute;
    top: 40px;
  }
  .help-buttons {
    width: 100vw;
    position: absolute;
    top: 0px;
    left: 0px;
    background-color: white;
  }
  .help-page-button {
    border-radius: 5px;
  }
  .tabs button {
    position: absolute;
    top: 0px;
  }
}

@media screen and (max-width: 600px) {
  .help-buttons {
    position: absolute;
    top: 0;
  }
  .help-content {
    height: 150vh;
    position: absolute;
    top: 40px;
  }
}
